

.App {

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.employees {
  margin: 85px 0 0;
  padding-left: 12px;
}

.employee-wrapper {
  color: #4c4b4c;
  display: -ms-flexbox;
  display: flex;
  letter-spacing: 0.03em;
  padding: 16px 12px 16px 0;
  border-bottom: 1px solid rgba(0,0,0,0.12);
  max-width: 600px;
  margin: 0 auto;
}

.employee-wrapper:last-of-type {
  border-style: none;
}

.employee-wrapper .profile {
  -ms-flex: 0 0 60px;
      flex: 0 0 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  background: #c0c0c0;
}

.employee-wrapper .profile.photo {
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.1);
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.1);
}

.employee-wrapper .profile span {
  color: white;
  display: block;
  width: 100%;
  text-align: center;
  font-size: 28px;
  line-height: 60px;
}

.employee-wrapper .profile img {
  height: 60px;
  width: 60px;
  text-indent:-9999px;
}



.employee-wrapper .details {
  margin-top: 8px;
  margin-left: 12px;
}

.employee-wrapper .details h3 {
  font-size: 18px;
  margin: 0;
}

mark {
  background-color: rgba(0,128,252,0.3);
  color: #000000;
  border-radius: 2px;
  padding: 3px 2px; 
  margin: 0 -2px;
}

.employee-wrapper .details ul {
  margin: 8px 0;
  padding: 0;
}

.employee-wrapper li {
  display: inline-block;
  list-style: none;
  font-size: 12px;
  line-height: 1.35;
  color: #555555;
}

.employee-wrapper li:after {
  content: 'â€¢';
  margin-left: 6px;
  margin-right: 6px;
}

.employee-wrapper li:last-of-type:after {
  display: none;
}

@media only screen and (min-width : 1000px) { /*1224 */
  .employees {
    padding-left: 16px;
  }

  .employee-wrapper {
    max-width: 800px;
  }

  .employee-wrapper .details {
    padding-left: 16px;
  }

  .employee-wrapper .details h3 {
    font-size: 22px;
  }

  .employee-wrapper .profile {
    height: 80px;
    -ms-flex: 0 0 80px;
        flex: 0 0 80px;
  }

  .employee-wrapper .profile img {
    height: 80px;
    width: 80px;
  }

  .employee-wrapper .profile span {
    line-height: 80px;
  }

  .employee-wrapper li {
    font-size: 16px;
  }
}

.search-wrapper {
  background: white;
  padding: 12px;
  position: fixed;
  top: 0;
  width: 100%;
  border-style: solid;
  border-width: 0 0 1px 0;
  border-color: rgba(0,0,0,0.12);
}

.search-wrapper .icon {
  margin: 0 auto;
  display: inline;
}

.search-wrapper input {
  background: #f0f0f0;
  border: none;
  border-radius: 4px;
  line-height: 1;
  padding: 14px 14px 14px 30px;
  width: 100%;
  font-size: 20px;
  max-width: 400px;
  margin: 0 auto;
  display: block;
  font-family: 'Roboto', sans-serif;
}

